* {
  font-family: sans-serif;
}

body {
  background-color: #ccc;
  padding: 60px;
}

.title-container {
  background-color: #fff;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  margin: 0 auto;
  text-align: center;
  width: 550px;
}

.attribution {
  font-size: 10px;
  margin: 0;
  padding: 0;
  /*margin-bottom: 10px;*/
}

.attribution a {
  color: #000;
  text-decoration: none;
}

.attribution a:visited {
  color: #7B0FFF;
  font-size: 16px;
}

.attribution a:hover {
  background: #7B0FFF;
  border-radius: 5px;
  color: #fff;
  padding: 0 5px;
}

h2.app-title {
  background: #fff;
  color: #000;
  display: inline-block;
  font-family: "Roboto Light", sans-serif;
  font-size: 60px;
  font-weight: 900;
  margin: 10px 0 0 0;
  mix-blend-mode: multiply;
  position: relative;
}

h2.app-title::before {
  background: linear-gradient(90deg, #C2C02E 10%, #22C119 28%, #00D4FF 49%, #7B0FFF 69%, #FF0000 90%);
  content: '';
  display: block;
  height: 100%;
  left: 0;
  mix-blend-mode: screen;
  position: absolute;
  top: 0;
  width: 100%;
}

.container {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  width: 100%;
}

input {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-color: transparent;
  border-style: solid;
  border-width: 1px;
  color: #554f60;
  font-size: 12px;
  height: 20px;
  margin: 0 auto;
  outline: none;
  padding-left: 10px;
  position: relative;
  top: -50px;
  width: 536px;
}

input:focus {
  border-bottom: 1px solid #7B0FFF;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  background-color: white;
  border: 1px solid grey;
  border-radius: 6px;
  box-shadow: 10px 10px 17px -5px rgba(153, 153, 153, 1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 30px;
  margin-right: 30px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 190px;
}
